.navbar {
  height: 4rem;
  background-color: rgba(248, 242, 218, 0.6);
  color: grey;
  position: sticky;
  text-align: center;
  font-family: "Julius Sans One", sans-serif;
  font-size: medium;
}

.navbar nav {
  height: 100%;
}

.navbar ul {
  height: 100%;
  list-style: none;
  display: flex;
  padding: 0;
  margin: 0;
  align-items: center;
  justify-content: center;
}

.navbar li {
  margin: 0 1rem;
  width: 10rem;
}

.navbar a {
  color: black;
  text-decoration: none;
}

.navbar a:hover,
.navbar a:active,
.navbar a.active {
  color: black;
  padding-bottom: 0.25rem;
  border-bottom: 2px solid black;
}
